/* ALL */
.Images_Images__3zDvr {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-top: 6rem;
}

.Images_Wrapper__2BpG3 {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #111;
    border-radius: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 2rem;
    margin-bottom: 3rem;
}
.Images_Info__FUp36 {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
}
.Images_Content__ScB8s, .Images_Buttons__3yo8E {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.Images_Heading__2aRa4 {
    color: var(--color-purple);
}

/* IMAGES */
.Images_Repository__lduol {
    width: 25rem;
}
.Images_Tag__XyP_U {
    width: 10rem;
}
.Images_Id__bJ_lJ {
    width: 10rem;
}
.Images_Created__32q_6 {
    width: 9.5rem;
}
.Images_Size__1PDK- {
    width: 15rem;
}

.Images_Repository__lduol, .Images_Tag__XyP_U, .Images_Id__bJ_lJ, .Images_Created__32q_6,
.Images_Size__1PDK- {
    overflow: hidden;
    margin-right: 1.25rem;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Images_Button__3-1NS {
    margin-right: 1.25rem !important;
}

.Images_Headline__1ibx2 {
    color: var(--color-purple);
}

.Images_Images__3zDvr h1,
.Images_Images__3zDvr h2 {
    font-weight: 500;
}

.Images_Images__3zDvr h1 {
    font-size: 1.8rem;
    margin-top: 0;
    margin-bottom: 0.8rem;
}

.Images_Images__3zDvr h2 {
    font-size: 1.6rem;
}

.Images_Images__3zDvr p {
    font-size: 1.1rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.Images_DetailTitle__1B6Tf {
    color: var(--color-purple);
    width: 4.5rem;
}

.Images_Error__3cwEP {
    color: red;
    white-space: pre;
    text-align: left !important;
}

/* desktop 1200 + */
@media (min-width: 1200px) {
    .Images_Images__3zDvr {
        margin-top: 10rem;
    }

    .Images_Wrapper__2BpG3 {
        width: 90%;
        padding: 4.5rem 2rem 5rem 2rem;
    }
    .Images_Info__FUp36 {
        flex-direction: column;
        justify-content: space-around;
        margin-top: 4rem;
    }

    .Images_Images__3zDvr h1 {
        font-size: 2.5rem;
    }

    .Images_Images__3zDvr p {
        margin-bottom: 1rem;
    }

    /* IMAGES */
    .Images_Repository__lduol {
        width: 20rem;
    }
    .Images_Tag__XyP_U {
        width: 5rem;
    }
    .Images_Id__bJ_lJ {
        width: 8rem;
    }
    .Images_Created__32q_6 {
        width: 9.5rem;
    }
    .Images_Size__1PDK- {
        width: 5rem;
    }
}

/* tabletLandscape 900 - 1199 */
@media (max-width: 1199px) {
    .Images_Repository__lduol {
        width: 20rem;
    }
    .Images_Tag__XyP_U {
        width: 4rem;
    }
    .Images_Id__bJ_lJ {
        width: 7rem;
    }
    .Images_Created__32q_6 {
        width: 7rem;
    }
    .Images_Buttons__3yo8E {
        margin-top: 1rem;
        margin-left: 1rem;
    }
    .Images_DetailTitle__1B6Tf {
        margin-left: 1.5rem !important;
    }
}

/* tabletPortrait 600 - 899 */
@media (max-width: 899px) {
    .Images_Repository__lduol {
        width: 17rem;
    }
    .Images_Id__bJ_lJ {
        width: 7rem;
    }
    .Images_Buttons__3yo8E {
        flex-direction: column-reverse;
    }
    .Images_Button__3-1NS {
        margin-top: 0.75rem !important;
    }
    .Images_DetailTitle__1B6Tf {
        margin-left: 1.5rem !important;
    }
}

/* phone < 600 */
@media (max-width: 599px) {
    .Images_Repository__lduol {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        word-break: break-word;
        margin-bottom: 1rem;
        margin-right: 0;
    }
    .Images_Size__1PDK- {
        width: 8rem;
    }
    .Images_DetailTitle__1B6Tf {
        margin-left: 0.2rem !important;
    }
}

